import React, {Component} from "react";
import ProfileCard from '../profile-card/ProfileCard';
import LeftPanel from '../left-panel/LeftPanel';

import './Dashboard.scss';

const axios = require('axios');

class Dashboard extends Component {

  constructor(props) {
    super(props);
    this.state = {
      Users: []
    }
  }

  getUsersData() {
    setTimeout(axios
      .get('https://api.clockify.me/api/v1/workspaces/5d95853fe12ea333c950a60e/users/', {
        headers: {
          'X-Api-Key': 'X6T12OdxL39DwatB',
          'Content-Type': null
        }
      })
      .then(res => {
        const data = res.data
        console.log(data)
        const users = data.map(u =>
          <div className="profile-list__item" key={u.id}>
            <ProfileCard name={u.name} bio={u.email} profileImg={u.profilePicture} timezone={u.settings.timeZone} />
          </div>
        )

        this.setState({
            users
        })

      })
      .catch((error) => {
          console.log(error)
      }), 8000)
    
  }

  componentWillMount(){
    this.getUsersData()
  }

  render() { 
    return (
      <div className="app-dashboard">
        <div className="app-dashboard__main-grid">
          <LeftPanel />
          <div className="app-dashboard__body">
            <h2>Users</h2>
            <div className="app-dashboard__profile-list-wrapper">
              {this.state.users}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
 
export default Dashboard;